import { useEffect, useRef, useState } from 'react'
import styled from 'styled-components'
import { bg } from '../../lib/web3js';

const Wrapper = styled.div`
display : flex;
font-size : 14px;
align-items : center;
background: rgba(85, 119, 253, 0.1);
border-radius: 4px;
justify-content: space-between;
input {
  background: transparent;
  border-radius: 4px;
  // min-width: 240px;
  border: 0;
  width : ${props => props.inputWidth};
  height: 56px;
  color : #E0ECFF;
  font-size : 18px;
  padding-left: 16px;
}
input:focus {
  outline: none;
}
.unit {
  margin-right: ${props => props.unitPadding};
}
`
export default function Input({value,unit,max,step,onChange,styles = {},focus,unitPadding = '24px',readOnly,inputWidth = '50%',className,disabled = false}){
  const inputRef = useRef(null);
  const change = e => {
    const {value} = e.target
    if(max && bg(value).gt(max)){    
      onChange && onChange(max)
    } else if(value <0 || isNaN(value)) {
      onChange && onChange('')
    } else {
      onChange && onChange(value)
    }
  }

  useEffect(()=>{
    if(inputRef.current && focus){
      inputRef.current.focus();
    }
  },[focus])


  return (
    <Wrapper  unitPadding={unitPadding} inputWidth={inputWidth} className={className} style={{...styles}}>
      <input type='number' disabled = {disabled} value={value} max={max}  min='0'  step = {step} ref={inputRef} onChange={change} readOnly={readOnly}/>
      <div className='unit'>{unit}</div>
    </Wrapper>    
  )
}